.form_city_weather{
    padding: 15px 0px;
    align-items: flex-start;
}
.input_city{
    flex-basis: 50%;
}
.input_submit_city{
    flex-basis: 50%;
    padding: 4px 20px 0px 20px;
    border-top: 2px solid #000000;

    
}
.input_submit input{
    padding: 0;
    border: none;
    background-color: transparent;
    color: #000000;
    font-size: 1.5em;
}

.input_text{
    width: 100%;
    padding: 8px 20px 2px 20px;
    background-color: transparent;
    border-left: none;
    border-top: none;
    font-size: 1.5em;
    border-bottom: 2px solid #000000;
    border-right: 2px solid #000000;
    color: #000000;
}
.input_text::placeholder{
    color: #000000;

    font-weight: 600;

    letter-spacing: 1.2px;
}
.input_submit_city{
    height: 100%;
}