.city_history_item{
    position: relative;
    width: 100%;
    padding: 20px 20px;
    margin: 1px 0;
    border-bottom: 2px solid #000000;
    
}
.city_history_item .city_title{
    padding: 0px;
    color: #000000;
    font-size: 1.5em;
    letter-spacing: 1.2px;
    word-break: break-word;
    z-index: 2;
}
.city_history_link{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    text-decoration: none;
    z-index: 2;
}


.btn_history{
    height: 100%;
    font-size: 1em;
    letter-spacing: 1.1px;
    color: #000000;
    text-transform: uppercase;
    

}