.weather_item{
  padding: 25px;
}
.weather_item_temp{
    padding-top: 25px;
    text-align: center;
    color: #414141;
    font-size: 5em;
}
.date_title,.city_title{
    padding-bottom: 5px;
    text-transform: uppercase;
    color: rgba(0,0,0,0.8);
    letter-spacing: 1.2px
}
.current_date,.current_city{
    font-size: 1.5em
}
.temperature_weather_icon{
    margin-top: 20px;
    align-items: center;
}
.current_temperature{
    font-size: 3.5em;
    line-height: .85;
}
.main_section{
    margin: 25px 0;
}
.weather_text{
    width: 80%;
}
.current_weather_text{
    font-size: 3em;
    word-spacing: 999em;
}
.weather_text_description{
    text-align: end;
}
.sunset_sunrise{
    margin-top: 20px;
}
.title_small{
    text-transform: uppercase;
    font-size: .85em;
    letter-spacing: 1.2px;
    color: rgba(0,0,0,0.8);
}
.current_humidity,.current_wind,.current_visibility,.current_pressure{
    padding-top: 5px;
    font-size: 1.4em;
    
}
.units{
    font-size: .75em;
    text-transform: uppercase;
}
.sunset_title,.sunrise_title{
    text-transform: uppercase;
    letter-spacing: 1.2px;
    color: rgba(0,0,0,0.8);
}
.current_sunrise,.current_sunset{
    font-weight: 500;
    padding-top: 5px;
    font-size: 1.9em;
}

.weather_forecast_day{
    width:100%;
    margin-top: 25px;
    overflow-x: scroll;
}
.other_info{
    padding-bottom: 15px;
}
.bottom_nav{
    padding-top: 15px;
    border-top: 2px solid rgba(0,0,0, 0.8);
}
.home_link{
    text-decoration: none;
    color: #000000;
}
