@font-face {
  font-family: 'Helvetica Neue Cyr Medium';
  src: url('./fonts/Helvetica/Helvetica-Medium/HelveticaNeueCyr-Medium.eot');
  src: url('./fonts/Helvetica/Helvetica-Medium/HelveticaNeueCyr-Medium.eot?#iefix') format('embedded-opentype'),
  url('./fonts/Helvetica/Helvetica-Medium/HelveticaNeueCyr-Medium.woff') format('woff'),
  url('./fonts/Helvetica/Helvetica-Medium/HelveticaNeueCyr-Medium.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: 'Helvetica Neue Cyr Medium', sans-serif;
 
  font-size: 14px;
  background-color: #ffffff;
 
}
html,body,#root,.mainPage{
  height: 100%;
}
body{
  margin: 0;
}
*{
  box-sizing: border-box;
}
p{
  margin: 0;
}
.row{
  display: flex;
}
.jstf-sp-btw{
  justify-content: space-between
}
.jstf-sp-arnd{
  justify-content: space-around
}
.jstf-flex-end{
  justify-content: flex-end;
}
.align-items-center{
  align-items: center
}
body, .mainPage{
  background-color: antiquewhite;
}
.weather_icon{
  width: 5vw;
  height: 5vw;
  min-width: 55px;
  min-height: 55px;
}
.weather_icon_mini{
  width: 3.5vw;
  height: 3.5vw;
  min-width: 35px;
  min-height: 35px;
}
.clouds  .weather_icon,.forecast .clouds  .weather_icon_mini {
  background: url("./img/cloud.png") no-repeat;
  background-position: center;
  background-size: cover;
}


.sunny .weather_icon,.forecast .sunny .weather_icon_mini, .clear .weather_icon,.forecast .clear .weather_icon_mini {
  background: url("./img/sun.png") no-repeat;
  background-position: center;
  background-size: cover;
}

.rain .weather_icon,.forecast .rain  .weather_icon_mini {
  background: url("./img/rain.png") no-repeat;
  background-position: center;
  background-size: cover;
}
.snow .weather_icon,.forecast .snow  .weather_icon_mini {
  background: url("./img/rain.png") no-repeat;
  background-position: center;
  background-size: cover;
}

.thunderstorm .weather_icon,.forecast .thunderstorm .weather_icon_mini {
  background: url("./img/thunderstorm.png") no-repeat;
  background-position: center;
  background-size: cover;
}

.drizzle .weather_icon,.forecast .drizzle .weather_icon_mini {
  background: url("./img/rain.png") no-repeat;
  background-position: center;
  background-size: cover;
}
.fog .weather_icon,.forecast .fog .weather_icon_mini,.mist .weather_icon,.forecast .mist .weather_icon_mini,.smoke .weather_icon,.forecast .smoke .weather_icon_mini,.haze .weather_icon,.forecast .haze .weather_icon_mini{
  background: url("./img/fog.png") no-repeat;
  background-position: center;
  background-size: cover;
}
.snow .weather_icon,.forecast .snow .weather_icon_mini {
  background: url("./img/snow.png") no-repeat;
  background-position: center;
  background-size: cover;
}
.sand .weather_icon,.forecast .sand .weather_icon_mini,.dust .weather_icon,.forecast .dust .weather_icon_mini{
  background: url("./img/sand.png") no-repeat;
  background-position: center;
  background-size: cover;
}
.tornado .weather_icon,.forecast .tornado .weather_icon_mini,.ash .weather_icon,.forecast .ash .weather_icon_mini,.squall .weather_icon,.forecast .squall .weather_icon_mini{
  background: url("./img/tornado.png") no-repeat;
  background-position: center;
  background-size: cover;
}
.night .rain .weather_icon{
  background: url("./img/rain_night.png") no-repeat;
  background-position: center;
  background-size: cover;
}

.night .sunny .weather_icon,.night  .clear .weather_icon{
  background: url("./img/moon.png") no-repeat;
  background-position: center;
  background-size: cover;
}

.night .clouds  .weather_icon {
  background: url("./img/cloud_night.png") no-repeat;
  background-position: center;
  background-size: cover;
}
.night .snow  .weather_icon {
  background: url("./img/snow_night.png") no-repeat;
  background-position: center;
  background-size: cover;
}
.nihgt .thunderstorm .weather_icon{
  background: url("./img/thunderstorm_night.png") no-repeat;
  background-position: center;
  background-size: cover;
}

.snow .weather_item, .snow .weather_forecast{
  background-color: #ffffff;
}
.dust .weather_item ,.sand .weather_item, .dust .weather_forecast,.sand .weather_forecast{
  background-color: #b28b52
}
.fog .weather_item ,.mist .weather_item,.smoke .weather_item ,.haze .weather_item, .fog .weather_forecast,.mist .weather_forecast,.smoke .weather_forecast,.haze .weather_forecast{
  background-color: ghostwhite;
}
.rain .weather_item, .rain .weather_forecast{
  background-color: #d9d8d6;
}
.clear .weather_item, .clear .weather_forecast{
  background-color: #eee8b5;
}
.clouds .weather_item, .clouds .weather_forecast{
  background-color: #b5c6ee;
}
.night .weather_item, .night .weather_forecast{
  background-color: #393f59;
  color: #ffffff;
}
.night .sunset_title,.night .sunrise_title,.night .title_small,.night .date_title,.night .city_title,.night .home_link{
   color: #ffffff;
}
.night .bottom_nav{
  border-top: 2px solid rgba(255,255,255, 0.8);
}
.night .weather_forecast_week_temp_night{
  color: rgba(255, 255, 255, 0.7)
}
