.weather_forecast_week_item{
    padding: 3px 0;
    margin: 20px 0;
}
.weather_forecast_week_temp{
    flex-basis: 35vw;
    justify-content: flex-end;
    align-content: flex-end;
}
.weather_forecast_week_temp_day{
    font-size: 2em;
}
.weather_forecast_week_temp_night{
    margin-left: 8px;
    font-size: 1.3em;
    color: rgba(0, 0, 0, 0.7)
}

.weather_forecast_week_day{
    flex-basis: 35vw;
    font-size: 1.5em;
}
.forecast_other_info .humidity{
    margin-left: 15px;
}
.weather_forecast_week_item .humidity_title,.weather_forecast_week_item .pressure_title,.weather_forecast_week_item .current_pressure,.weather_forecast_week_item .current_humidity{
    margin-top: 5px;
}